import React, { Component } from 'react';
import { Row, Col } from 'antd';
import { Form, Input, Button, Select, Radio, DatePicker } from 'antd';
import './App.css';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;

const eng = {
  type_number: 'Please input a valid number',
  required: 'Please fill out this field',
  bt: 'Boat',
  pd: 'Personal details',
  mft: 'Manufacturer',
  md: 'Model',
  yr: 'Year built',
  vt: 'Value of trailer',
  vv: 'Value of vessel',
  is: 'Insurance sum',
  tv: 'Type of vessel',
  mb: 'Motorboot',
  sb: 'Sailing boat',
  tep: 'Total engine power',
  vom: 'Value of outboard motor',
  ca: 'Cruising Area',
  ec: 'Europe coast',
  ei: 'Europe Inside',
  sal: 'Salutation',
  mr: 'Mr.',
  mrs: 'Mrs.',
  ln: 'Last name',
  fn: 'First name',
  db: 'Date of birth',
  tel:  'Phone number',
  zc: 'Zip code',
  str: 'Street',
  cty: 'City',
  mail: 'Email',
}

const ger = {
  type_number: 'Bitte geben Sie eine gültige Nummer ein',
  required: 'Bitte füllen Sie dieses Feld aus',
  bt: 'Boot',
  pd: 'Ihre Kontaktdaten',
  mft: 'Hersteller ',
  md: 'Modell ',
  yr: 'Baujahr ',
  vt: 'Wert Trailer',
  vv: 'Kaufpreis ',
  is: 'Versicherungssumme',
  tv: 'Schiffstyp ',
  mb: 'Motorboot',
  sb: 'Segelboat',
  tep: 'Gesamtmotorleistung',
  vom: 'Wert Außenborder',
  ca: 'Fahrtgebiet',
  ec: 'Europa küste',
  ei: 'Europa binnen',
  sal: 'Anrede',
  mr: 'Herr',
  mrs: 'Frau',
  ln: 'Nachname',
  fn: 'Vorname',
  db: 'Geburtsdatum',
  tel:  'Telefon',
  zc: 'Postleitzahl',
  str: 'Straße',
  cty: 'Stadt',
  mail: 'E-Mail',
}

class PantForm extends Component {

  state = {
    content: eng,
  };

  handleLanguage = (e) => {
    if(e==='eng'){
      this.setState({ content: eng});
    }else{
      this.setState({ content: ger});
    }
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    //const { autoCompleteResult } = this.state;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 10 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    const colLayout = {
      xs: 24, sm: 12, md: 12,
    }

    const currency = (
      <Select defaultValue="USD" style={{ width: 80 }}>
        <Option value="USD">USD</Option>
        <Option value="EUR">EUR</Option>
      </Select>
    )

    const powerUnity = (
      <Select defaultValue="PS" style={{ width: 80 }}>
        <Option value='PS'>PS</Option>
        <Option value='KW'>KW</Option>
      </Select>
    )


    var content = this.state.content;

    return (
      <Form onSubmit={this.handleSubmit}>

        <Row gutter={8}>
        <Col {...colLayout}>
          <FormItem
            {...formItemLayout}
            label='Language'
          >

              <Select onChange={this.handleLanguage} defaultValue='eng'>
                <Option value='eng'>English</Option>
                <Option value='ger'>German</Option>
              </Select>

          </FormItem>
        </Col>
      </Row>

      <Row>
        <Col offset={2}>
          <h1>{content.bt}</h1>
        </Col>
      </Row>

        <Row gutter={8}>
          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.mft}
            >
              {getFieldDecorator('mtf', {
                rules: [{
                  type: 'string', message: '',
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>
          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.md}
            >
              {getFieldDecorator('md', {
                rules: [{
                  type: 'string', message: '',
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.yr}
            >
              {getFieldDecorator('yr', {
                rules: [{
                  required: true, message: content.required,
                }],
              })(
                <Select >
                  <Option value="1990">1990</Option>
                  <Option value="1991">1991</Option>
                  <Option value="1992">1992</Option>
                  <Option value="1993">1993</Option>
                  <Option value="1994">1994</Option>
                  <Option value="1995">1995</Option>
                  <Option value="1996">1996</Option>
                  <Option value="1997">1997</Option>
                  <Option value="1998">1998</Option>
                  <Option value="1999">1999</Option>
                  <Option value="2000">2000</Option>
                  <Option value="2001">2001</Option>
                  <Option value="2002">2002</Option>
                  <Option value="2003">2003</Option>
                  <Option value="2004">2004</Option>
                  <Option value="2005">2005</Option>
                  <Option value="2006">2006</Option>
                  <Option value="2007">2007</Option>
                  <Option value="2008">2008</Option>
                  <Option value="2009">2009</Option>
                  <Option value="2010">2010</Option>
                  <Option value="2011">2011</Option>
                  <Option value="2012">2012</Option>
                  <Option value="2013">2013</Option>
                  <Option value="2014">2014</Option>
                  <Option value="2015">2015</Option>
                  <Option value="2016">2016</Option>
                  <Option value="2017">2017</Option>
                </Select>
              )}
            </FormItem>
          </Col>
          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.vt}
            >
              {getFieldDecorator('vt', {
                rules: [{
                  pattern: '^[0-9]*$' , message: content.type_number,
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input addonAfter={currency}/>
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.vv}
            >
              {getFieldDecorator('vv', {
                rules: [{
                  pattern: '^[0-9]*$' , message: content.type_number,
                },{
                  required: true, message: content.required,
                }],
              })(
                <Input addonAfter={currency}/>
              )}
            </FormItem>
          </Col>
          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.is}
            >
              {getFieldDecorator('is', {
                rules: [{
                  pattern: '^[0-9]*$' , message: content.type_number,
                },{
                  required: true, message: content.required,
                }],
              })(
                <Input addonAfter={currency}/>
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.tv}
            >
              {getFieldDecorator('tv', {
                rules: [{
                  required: true, message: content.required,
                }],
              })(
                <Select >
                  <Option value={content.mb}>{content.mb}</Option>
                  <Option value={content.sb}>{content.sb}</Option>
                </Select>
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.tep}
            >
              {getFieldDecorator('tep', {
                rules: [{
                  pattern: '^[0-9]*$' , message: content.type_number,
                },{
                  required: true, message: content.required,
                }],
              })(
                <Input addonAfter={powerUnity}/>
              )}
            </FormItem>
          </Col>
          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.vom}
            >
              {getFieldDecorator('vom', {
                rules: [{
                  pattern: '^[0-9]*$' , message: content.type_number,
                },{
                  required: true, message: content.vom,
                }],
              })(
                <Input addonAfter={currency}/>
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.ca}
            >
              {getFieldDecorator('ca', {
                rules: [{
                  required: true, message: content.required,
                }],
              })(
                <Select >
                  <Option value={content.ec}>{content.ec}</Option>
                  <Option value={content.ei}>{content.ei}</Option>
                </Select>
              )}
            </FormItem>
          </Col>
          </Row>

          <Row>
            <Col offset={2}>
              <h1>{content.pd}</h1>
            </Col>
          </Row>


          <Row gutter={8}>
          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.sal}
            >
              {getFieldDecorator('sal', {
                rules: [{
                  required: true, message: content.required,
                }],
              })(
                <RadioGroup>
                  <Radio value={content.mr}>{content.mr}</Radio>
                  <Radio value={content.mrs}>{content.mrs}</Radio>
                </RadioGroup>
              )}
            </FormItem>
          </Col>
        </Row>

        <Row gutter={8}>
          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.ln}
            >
              {getFieldDecorator('ln', {
                rules: [{
                  type: 'string', message: '',
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.fn}
            >
              {getFieldDecorator('fn', {
                rules: [{
                  type: 'string', message: '',
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.db}
            >
              {getFieldDecorator('db', {
                rules: [{
                  required: true, message: content.required,
                }],
              })(
                <DatePicker />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.tel}
            >
              {getFieldDecorator('tel', {
                rules: [{
                  pattern: '^[0-9]*$' , message: content.type_number,
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.zc}
            >
              {getFieldDecorator('zc', {
                rules: [{
                  pattern: '^[0-9]*$' , message: content.type_number,
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.str}
            >
              {getFieldDecorator('str', {
                rules: [{
                  type: 'string' , message: '',
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.cty}
            >
              {getFieldDecorator('cty', {
                rules: [{
                  type: 'string' , message: '',
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

          <Col {...colLayout}>
            <FormItem
              {...formItemLayout}
              label={content.mail}
            >
              {getFieldDecorator('mail', {
                rules: [{
                  type: 'email' , message: '',
                }, {
                  required: true, message: content.required,
                }],
              })(
                <Input />
              )}
            </FormItem>
          </Col>

        </Row>

        <Row >
        <Col xs={{ offset: 0 }} sm={{ offset: 5 }} >
          <Button type="primary" htmlType="submit" className="Submitbtn">
            Submit
          </Button>
        </Col>
      </Row>

      </Form>
    );
  }
}

const App = Form.create()(PantForm);

export default App;



